.home_header{
  position: relative;
  height: 100vh;
  display: flex;
  align-items: center;
}
.header_bg{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}
.home_header .sub_heading{
  font-weight: 400;
  line-height: 1.5;
}
.button_row{
  margin-top: 2rem;
  /* margin-bottom: 1.5rem; */
}
.red_btn{
  background-color: #ff6363;
  color: #fff;
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem 2.25rem;
  border-radius: 0.5rem;
  border: 2px solid #ff6363;
  margin-right: 1rem;
  transition: all 0.2s ease-in-out;
}
.red_btn:hover{
  background-color: #fd4c4c;
}
.black_btn{
  color: black;
  background-color: #fff;
  font-size: 1.5rem;
  font-weight: 600;
  padding: 1rem 2.25rem;
  border-radius: 0.5rem;
  border: 2px solid black;
  transition: all 0.2s ease-in-out;
}
.black_btn:hover{
  border: 3px solid #ff6363;
    background-color: #ff6363;
    color: white;
}
.mobile_left{
